/*  ------------ TOP SECTION -------------- */
.deck-page-wrapper .top-section {
  flex-direction: column;
}

.top-section .deck-description {
  display: flex;
}

.top-section .deck-description .text {
  font-size: 14px;
  line-height: 32px;
  width: 87%;
  white-space: pre-wrap;
}

/* ------------ BOTTOM SECTION ------------ */

/* ------------ MOVE HERE BUTTON ------------ */ 
.bottom-section .move-here-btn {
  background: none;
  border: 2px dotted #0171ff;
  margin-bottom: 5px;
  width: 100%;
}

.bottom-section .move-here-btn:hover {
  border-color: #68a9fd;
}


/* ------------ MOBILE --------------  */

.settings-menu-wrapper {
  align-items: flex-start;
  background-color: #000;
  bottom: 50px;
  display: flex;
  height: auto;
  position: fixed;
  width: auto;
  padding: 10px;
}

.settings-menu {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

@media (max-width: 500px) {
  /* ------------ BOTTOM SECTION --------------  */

  .deck-page-wrapper .bottom-section {
    margin-bottom: 100px;
  }
}
